import React from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

type Props = {
  children: React.ReactNode;
  allowedRoles?: string[]; // e.g., ['donor', 'bloodbank', 'superadmin']
  requireApproved?: boolean; // for bloodbank access
};

export default function ProtectedRoute({ children, allowedRoles, requireApproved }: Props) {
  const { user, loading } = useAuth();

  if (loading) return null; // or a loading spinner

  if (!user) {
    return <Navigate to="/auth" replace />;
  }

  const role = (user.userType || user.type || '').toString().toLowerCase();
  // If token claim marks the user as superAdmin, prefer that.
  const isSuperAdmin = !!(user.claims && (user.claims.superAdmin || user.claims.superadmin));
  const normalizedRole = isSuperAdmin ? 'superadmin' : role.includes('blood') ? 'bloodbank' : role.includes('admin') ? 'superadmin' : 'donor';

  if (allowedRoles && !allowedRoles.includes(normalizedRole)) {
    return <Navigate to="/" replace />;
  }

  if (requireApproved && normalizedRole === 'bloodbank') {
    const approved = !!user.approved;
    const status = (user.status || '').toString().toLowerCase();
    if (status === 'rejected') {
      return <Navigate to="/access-revoked" replace />;
    }
    if (!approved) return <Navigate to="/" replace />;
  }

  return <>{children}</>;
}
