
// src/firebase/auth.ts
import { 
  getAuth, 
  createUserWithEmailAndPassword, 
  signInWithEmailAndPassword, 
  signOut as firebaseSignOut,
  onAuthStateChanged,
  deleteUser
} from 'firebase/auth';
import { doc, getDoc, deleteDoc, onSnapshot } from 'firebase/firestore';
import { getIdTokenResult } from 'firebase/auth';
import { db } from './index';
import type { User } from './types';
import { createUserProfile, createBloodBank } from './services';

const auth = getAuth();

/**
 * Creates a new donor user.
 */
export const signUpDonor = (email: string, password: string) => {
  return createUserWithEmailAndPassword(auth, email, password);
};

/**
 * Sign up a donor and create their Firestore profile.
 * @param email
 * @param password
 * @param profileData Fields to store in `users` collection (fullName, userType, etc.)
 */
export const signUpDonorProfile = async (email: string, password: string, profileData: Omit<User, 'uid' | 'createdAt' | 'updatedAt'>) => {
  const userCredential = await createUserWithEmailAndPassword(auth, email, password);
  const uid = userCredential.user.uid;
  try {
    await createUserProfile(uid, profileData);
    return userCredential;
  } catch (err) {
    // Cleanup: try to remove created auth user to avoid orphaned accounts
    try {
      await deleteUser(userCredential.user);
    } catch (delErr) {
      console.error('Failed to delete auth user after profile write failure', delErr);
    }
    throw err;
  }
};

/**
 * Signs in a user (donor or blood bank).
 * For blood banks, it performs an additional check to ensure they are approved.
 */
export const signIn = async (email: string, password: string) => {
  const userCredential = await signInWithEmailAndPassword(auth, email, password);
  const user = userCredential.user;

  // Check the user's type from the Firestore 'users' collection
  const userDocRef = doc(db, 'users', user.uid);
  const userDoc = await getDoc(userDocRef);

  if (userDoc.exists()) {
    const userData = userDoc.data() as User;
    
    // If the user is a blood bank, check if they are approved
    if (userData.userType === 'bloodBank') {
      const bankDocRef = doc(db, 'blood_banks', user.uid);
      const bankDoc = await getDoc(bankDocRef);
      const bankData = bankDoc.exists() ? bankDoc.data() as any : null;
      // If bank doesn't exist or is not approved, block sign in
      if (!bankDoc.exists() || !bankData.approved) {
        await firebaseSignOut(auth);
        throw new Error('This blood bank account is not approved. Please contact an administrator.');
      }
      // If bank was explicitly rejected, block and show revoked message
      if (bankData.status === 'rejected') {
        await firebaseSignOut(auth);
        throw new Error('This account has been revoked. Contact an administrator.');
      }
    }
    return userCredential;
  } else {
    await firebaseSignOut(auth);
    throw new Error('User data not found.');
  }
};

/**
 * Sign up a blood bank (facility) user and create both the `users` and `blood_banks` documents.
 * The facility will be created with `approved: false` by default.
 */
export const signUpBloodBank = async (
  email: string,
  password: string,
  userProfileData: Omit<User, 'uid' | 'createdAt' | 'updatedAt'>,
  bloodBankData: Omit<import('./types').BloodBank, 'id' | 'createdAt' | 'updatedAt'>
) => {
  const userCredential = await createUserWithEmailAndPassword(auth, email, password);
  const uid = userCredential.user.uid;
  try {
    await createUserProfile(uid, userProfileData);
    await createBloodBank(uid, bloodBankData);
    return userCredential;
  } catch (err) {
    // Attempt best-effort cleanup: remove any created docs and auth user
    try {
      await deleteDoc(doc(db, 'users', uid));
    } catch (e) {
      console.warn('Failed to delete users doc during rollback', e);
    }
    try {
      await deleteDoc(doc(db, 'blood_banks', uid));
    } catch (e) {
      console.warn('Failed to delete blood_banks doc during rollback', e);
    }
    try {
      await deleteUser(userCredential.user);
    } catch (delErr) {
      console.error('Failed to delete auth user during rollback', delErr);
    }
    throw err;
  }
};

/**
 * Signs out the current user.
 */
export const signOut = () => {
  return firebaseSignOut(auth);
};

/**
 * Listens for authentication state changes and retrieves user role.
 */
export const authStateListener = (callback: (user: (User & { uid: string; claims?: any }) | null | any) => void) => {
  const unsubAuth = onAuthStateChanged(auth, async (user) => {
    if (!user) {
      callback(null);
      return;
    }

    const userDocRef = doc(db, 'users', user.uid);
    const userDoc = await getDoc(userDocRef);
    // fetch ID token result to obtain custom claims
    let claims = {};
    try {
      const idResult = await getIdTokenResult(user);
      claims = idResult.claims || {};
    } catch (e) {
      console.warn('Failed to fetch ID token result', e);
    }

    const base = userDoc.exists() ? { ...user, ...userDoc.data() } : { ...user };

    // If the user is a blood bank, subscribe to their bank doc so we get real-time approved/status updates
    let unsubBank: (() => void) | null = null;
    try {
      const userType = (base as any).userType || (base as any).type || '';
      const normalized = userType.toString().toLowerCase();
      // Only treat as a blood bank when the userType explicitly indicates it
      const isBank = normalized === 'bloodbank' || normalized === 'blood_bank' || normalized === 'blood-bank' || userType === 'bloodBank';
      if (isBank) {
        const bankRef = doc(db, 'blood_banks', user.uid);
        const bankSnap = await getDoc(bankRef);
        const bankData = bankSnap.exists() ? bankSnap.data() : {};
        callback({ ...base, claims, ...bankData });

        // subscribe to future changes
        unsubBank = onSnapshot(bankRef, (snap) => {
          const bd = snap.exists() ? snap.data() : {};
          callback({ ...base, claims, ...bd });
        });
        return;
      }
    } catch (e) {
      console.warn('Failed to attach bank doc listener', e);
    }

    // Fallback: if not a bank user, just callback with base + claims
    callback({ ...base, claims });
    // no return here; unsubBank will be cleaned up by combined unsubscribe below if created
  });

  // Return a combined unsubscribe that also stops any bank snapshots
  return () => {
    try {
      unsubAuth();
    } catch (e) {}
  };
};
